@media (min-width: 200px) and (max-width: 767px) {
  .wrapper {
    width: 340px;
    margin: 0 auto;
  }
}

@media (min-width: 768px) and (max-width: 1150px) {
  .wrapper {
    width: 768px;
    margin: 0 auto;
  }
}

@media (min-width: 1150px) {
  .wrapper {
    width: 1150px;
    margin: 0 auto;
  }
}
